<template>
<div class="ui text container">
  <div class="ui warning message">
    <p>Note for the best experience please use either Google Chrome, Apple Safari, or Microsoft Edge. There are known graphical glitches on Mozilla Firefox and Internet Explorer. </p>
  </div>
  <div class="ui message" v-for="motd in motds">
    <p>Update {{motd.createdOn | formatDate}}</p>
    <span v-html="motd.message"> </span>
  </div>
  <div class="ui center aligned segment">
    <h1 class="ui icon header">
          <i class="help circle icon"></i>
          <div class="content">
            About Inquire
          </div>
        </h1>
    <br>
    <span>Inquire is an intelligent application that integrates with Cisco Spark.  </span>
  </div>
  <div class="ui center aligned top attached segment block header">
    <div class="item">
      <h1 class="ui header">Inquire has 3 main functions</h1>
    </div>
    <p></p>
  </div>

  <div class="ui left aligned attached segment">
    <div class="ui relaxed divided items">
      <div class="item">
        <i class="bullseye icon"></i>
        <div class="content">
          <div class="header">
            Ask Questions
          </div>
          <div class="description">
            Capture questions that were asked inside a Spark Space.
          </div>
        </div>
      </div>

      <div class="item">
        <i class="bullseye icon"></i>
        <div class="content">
          <div class="header">
            Get Answers
          </div>
          <div class="description">
            Provide a way to answer those questions and ensure the original questioner receives the information they need.
          </div>
        </div>
      </div>

      <div class="item">
        <i class="bullseye icon"></i>
        <div class="content">
          <div class="header">
            Gather Knowledge
          </div>
          <div class="description">
            Assemble a knowledgebase of all the information that is gathered amongst Spark Spaces.
          </div>
        </div>
      </div>

    </div>
  </div>
</div>
</template>

<script>
import {
  mapState
}
from 'vuex'
import axios from 'axios'

export default {
  data() {
    return {
      motds: []
    }
  },
  computed: mapState(['isAuthenticated', 'baseUrl']),
  methods: {
    getMotd() {
      let motdUrl = this.baseUrl + '/api/motd'
      axios.get(motdUrl).then(response => {
        this.motds = response.data
      })
    }
  },
  mounted: function() {
    this.getMotd()
  }
}
</script>



