import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    isAuthenticated: false,
    baseUrl: '',
    currentUser: {},
    userSpaces: [],
    userSpacesLoaded: false,
    apiUrl: ''
  },
  actions: {
    init(context) {
      let base;
      if (window.location.href.match(/(.*)\/public.*/i))
        base = window.location.href.match(/(.*)\/public.*/i)[1]
      else
        base = 'https://docker.chhab.rocks/inquire'
        // base = window.location.href.match(/^(.*)\/#/)[1]
      let opts = {
        baseUrl: base,
        apiUrl: base + '/api/spaces/'
      }
      context.commit('setInit', opts)
    },
    loadUser: function (context) {
      axios.get(context.getters.userUrl, {
        withCredentials: true
      }).then((response) => {
        context.commit('setUser', response.data)
      }, (err) => {
        console.error(err)
      })
    },
    loadAuthentication: function (context) {
      axios.get(context.getters.authUrl, {
        withCredentials: true
      }).then((response) => {
        context.commit('setAuthentication', response.data.isAuthenticated)
      }, (err) => {
        console.error(err)
      })
    },
    loadSpaces: function (context) {
      let url = 'https://api.ciscospark.com/v1/teams/'
      let config = {
        headers: {
          'Authorization': "Bearer " + context.state.currentUser.token
        }
      };
      axios.get(context.getters.spacesUrl, {
        withCredentials: true
      }).then((response) => {
        let promises = [];
        for (let [index, space] of response.data.entries()) {
          if (space.teamId && !space.teamName) {
            let teamUrl = url + space.teamId
            promises.push(axios.get(teamUrl, config).then(team => {
              response.data[index].teamName = team.data.name
            }).catch(err => {
              console.error(err)
              response.data[index].teamName = 'Unauthorized to view team name'
            }))
          }
        }
        Promise.all(promises).then(() => {
          context.commit('setSpaces', response.data)
        })
      }, (err) => {
        console.error(err)
      })
    },
    logOut: function (context) {
      axios.get(context.getters.logoutUrl, {
        withCredentials: true
      }).then((response) => {
        context.commit('setLogout', response.data.loggedOut)
      }, (err) => {
        console.error(err)
      })
    },
    setSpacesLoading: function (context) {
      context.commit('setSpacesLoading', true)
    }
  },
  mutations: {
    setInit(state, opts) {
      state.baseUrl = opts.baseUrl
      state.apiUrl = opts.apiUrl
    },
    setUser: (state, user) => {
      state.currentUser = user
    },
    setAuthentication: (state, isAuthenticated) => {
      state.isAuthenticated = isAuthenticated
    },
    setSpaces: (state, response) => {
      state.userSpaces = response
      state.userSpacesLoaded = true
    },
    setSpacesLoading: (state, value) => {
      state.userSpacesLoaded = false
    },
    setLogout: (state, loggedOut) => {
      state.isAuthenticated = !loggedOut
    }
  },
  getters: {
    authUrl: state => {
      return state.baseUrl + '/auth/isAuthenticated'
    },
    loginUrl: state => {
      return state.baseUrl + '/auth/login'
    },
    logoutUrl: state => {
      return state.baseUrl + '/auth/logout'
    },
    userUrl: state => {
      return state.baseUrl + '/auth/getUser'
    },
    spaceUrl: state => {
      return state.apiUrl + 'listSpaces'
    },
    spacesUrl: state => {
      return state.baseUrl + '/api/listSpaces'
    }
  },
  modules: {

  }
})


export default store



